var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("神奇女侠")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        神奇女侠是一部虚构的超级英雄，出现在DC Comics出版的美国漫画书中。角色是正义联盟的创始成员。这个角色首次出现在1941年10月的All Star Comics＃8中，她的第一部作品是1942年1月的Sensation Comics＃1。除了1986年的短暂中断外，DC Comics几乎连续出版了神奇女侠的头衔。在她的家乡，Themyscira岛国，她的官方头衔是Themyscira的戴安娜王妃，Hippolyta的女儿。当融入她家乡以外的社会时，她采用了她的平民身份戴安娜王子。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        神奇女侠是由美国心理学家和作家威廉莫尔顿马斯顿（笔名：查尔斯莫尔顿），和艺术家哈里G.彼得创建的。马斯顿的妻子伊丽莎白，和他们的生活伴侣，橄榄伯恩，记为他的角色的外观灵感。马斯顿漫画功能上他的想法DISC理论，和字符汲取灵感大量从早期的女权主义者，特别是从节育倡导者玛格丽特桑格 ; 特别是她的作品“ 女人与新种族 ”。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        神奇女侠的起源故事讲述了她的母亲女王希波吕忒（Hippolyta）从粘土中雕刻出来，并被赋予了像亚马逊一样生活的生活，以及希腊众神的超人力量。近年来，DC改变了自己的背景，因为她是Zeus和Hippolyta 的女儿，她的母亲和她的姑妈Antiope和Menalippe共同抚养。几十年来，这个角色的描写发生了变化，包括在20世纪70年代完全失去了她的权力; 到了20世纪80年代，艺术家乔治佩雷斯给了她一个肌肉发达的外观，并强调了她的亚马逊传统。她拥有先进的技术库，包括“真理的套索”，一对坚不可摧的手镯，一种用作弹丸的头饰，以及旧版故事中基于亚马逊技术的一系列设备。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        神奇女侠的角色是在第二次世界大战期间创造的; 故事中的角色最初描绘的是与Axis军队以及各种各样的色彩超级恶魔进行战斗，尽管随着时间的推移，她的故事更加强调希腊神话中的人物，神灵和怪物。很多故事都描绘了神奇女侠从束缚中解救出来的自杀，这种束缚打败了20世纪40年代漫画中常见的“ 苦恼中的少女 ”。自首次亮相以来的几十年里，神奇女侠已经获得了一大堆消灭亚马逊的敌人，其中包括战神，猎豹等经典恶棍。医生Poison，Circe，Doctor Psycho和Giganta，以及最近的对手，如Veronica Cale和First Born。神奇女侠也经常出现在超级英雄团队Justice Society（1941年）和Justice League（1960年）的漫画书中。")])]),_c('v-card'